document.addEventListener("DOMContentLoaded", function() {

  //* search
  const searchOpen = document.querySelector('.search__btn-open')
  const searchForm = document.querySelector('.search__form')
  const searchClose = document.querySelector('.search__btn-close')

  searchOpen.addEventListener('click', function () {
    searchForm.classList.add('search__form--active')
    searchOpen.classList.add('search__btn-open--active')
  })

  searchClose.addEventListener('click', function () {
    searchForm.classList.remove('search__form--active')
    searchOpen.classList.remove('search__btn-open--active')
  })

  document.addEventListener("click", function(e) {
    let target = e.target
    if (!target.closest(".search")) {
    searchForm.classList.remove("search__form--active")
      searchForm.querySelector("input").value = ""
      searchOpen.classList.remove("search__btn-open--active")
    }
  })

  //* burger
  const burgerOpen = document.querySelector('.burger')
  const burgerClose = document.querySelector('.burger-close')
  const menu = document.querySelector('.menu__list')
  const myBody = document.querySelector('body')
  const headerBot = document.querySelector('.header-bottom')
  const burgerMenuLink = document.querySelectorAll('.menu__link')

  burgerOpen.addEventListener('click', function() {
    menu.classList.add('menu__list--active')
    burgerOpen.classList.add('burger--active')
    burgerClose.classList.add('burger-close--active')
    headerBot.classList.add('header-bottom--active')
    if (menu.classList.contains('menu__list--active')){
      myBody.style.setProperty('overflow', 'hidden')
    }else {
      myBody.style.removeProperty('overflow')
    }
  })

  burgerClose.addEventListener('click', function() {
    burgerOpen.classList.remove('burger--active')
    burgerClose.classList.remove('burger-close--active')
    headerBot.classList.remove('header-bottom--active')
    menu.classList.remove('menu__list--active')
    myBody.style.removeProperty('overflow')
  })

  burgerMenuLink.forEach(el => el.addEventListener('click', function() {
    menu.classList.remove('menu__list--active')
    burgerOpen.classList.remove('burger--active')
    burgerClose.classList.remove('burger-close--active')
    headerBot.classList.remove('header-bottom--active')
    myBody.style.removeProperty('overflow')
  }))



  //* map
  ymaps.ready(init)
    function init(){

        var myMap = new ymaps.Map("map", {
            center: [55.762809, 37.624040],
            zoom: 14
        })

        var myPlacemark = new ymaps.Placemark( [55.769535, 37.639985], {}, {
          iconLayout: 'default#image',
          iconImageHref: '../img/icons/map-marker.svg',
          iconImageSize: [12, 12],
          iconImageOffset: [-10, -10]
        })

        myMap.geoObjects.add(myPlacemark)
        myMap.controls.remove('zoomControl')
        myMap.controls.remove('geolocationControl')
        myMap.controls.remove('routeButtonControl')
        myMap.controls.remove('trafficControl')
        myMap.controls.remove('typeSelector')
        myMap.controls.remove('fullscreenControl')
        myMap.controls.remove('rulerControl')
        myMap.controls.remove('searchControl')
        myMap.controls.remove('copyrights')
    }

    //* contact info switcher
    const infoOpen = document.querySelector('.contacts__show-info')
    const infoClose = document.querySelector('.contacts__btn-close')
    const contactsInfo = document.querySelector('.contacts__info')

    infoClose.addEventListener('click', function() {
      infoClose.classList.remove('contacts__btn-close--active')
      infoOpen.classList.add('contacts__show-info--active')
      contactsInfo.classList.remove('contacts__info--active')
    })

    infoOpen.addEventListener('click', function() {
      infoClose.classList.add('contacts__btn-close--active')
      infoOpen.classList.remove('contacts__show-info--active')
      contactsInfo.classList.add('contacts__info--active')
    })


    //* greensock animation
  let tlOpen = gsap.timeline()
  tlOpen.from(".hero__card-big", {
          y: 250,
          opacity: 0,
          duration: 0.65,
          ease: "power3.out",
        }, 1)
        .from(".hero__title", {
          x: 450,
          opacity: 0,
          duration: 0.75,
          ease: "power3.out",
        }, 1.5)
        .from(".hero__card-semi", {
          x: 450,
          opacity: 0,
          duration: 0.85,
          ease: "power3.out",
        }, 1)
        .from(".hero__card-mini", {
          y: 150,
          opacity: 0,
          duration: 0.85,
          ease: "power3.out",
        }, 1.1)
        .from(".hero__card-mini2", {
          y: 150,
          opacity: 0,
          duration: 0.85,
          ease: "power3.out",
        }, 1.3)


  //* validate about
  const aboutValidate = new JustValidate('.about__form', {
    errorFieldCssClass: 'field-invalid',
    errorLabelCssClass: 'label-invalid',
    successFieldCssClass: 'field-success',
  });

  aboutValidate
    .addField('#about-email', [
      {
        rule: 'required',
        errorMessage: 'Введите Ваш E-mail',
      },
      {
        rule: 'email',
        errorMessage: 'Недопустимый формат',
      },
    ])


  //* validate contacts
  const contactsValidate = new JustValidate('.c-form__form', {
    errorFieldCssClass: 'field-invalid',
    errorLabelCssClass: 'label-invalid',
    successFieldCssClass: 'field-success',
  });

  contactsValidate
    .addField('#c-name', [
      {
        rule: 'minLength',
        value: 3,
        errorMessage: 'Минимум 3 символа',
      },
      {
        rule: 'maxLength',
        value: 20,
        errorMessage: 'Максимум 20 символов',
      },
    ])
    .addField('#c-email', [
      {
        rule: 'required',
        errorMessage: 'Введите Ваш E-mail',
      },
      {
        rule: 'email',
        errorMessage: 'Недопустимый формат',
      },
    ])

});
